@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;600&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
  font-family: "Josefin Sans", sans-serif;
}

body {
  min-height: 100vh;
  background: linear-gradient(135deg, white, #fff5f5);
  position: relative;
}

.logo {
  padding: 30px;
}
.logo__img {
  width: 100px;
}

.main {
  display: flex;
  flex-direction: column-reverse;
}
.main .hero {
  background-image: url(../images/hero-mobile.jpg);
  width: 100%;
  height: 250px;
  background-size: cover;
  background-repeat: no-repeat;
}
.main .content {
  padding: 0 30px;
}
.main .content__title {
  font-size: 2.6rem;
  text-transform: uppercase;
  letter-spacing: 0.6rem;
  color: #413a3a;
  padding: 65px 0 20px 0;
  text-align: center;
}
.main .content__title span {
  font-weight: 300;
  color: #ce9797;
}
.main .content__txt {
  font-size: 0.9rem;
  font-weight: 600;
  color: #ce9797;
  line-height: 1.4;
  padding-bottom: 40px;
  text-align: center;
}
.main .content__form {
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 10px;
}
.main .content__form .form-input {
  padding: 16px 24px;
  width: 100%;
  border: 1px solid #ce9797;
  border-radius: 30px;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 600;
  background: transparent;
}
.main .content__form .form-input::placeholder {
  color: #ce9797;
}
.main .content__form .form-input-error {
  border: 2px solid #f96262;
}
.main .content__form .form-error-icon {
  position: absolute;
  right: 75px;
}
.main .content__form .form-submit-btn {
  height: 100%;
  min-width: 65px;
  background: linear-gradient(135deg, #f8bfbf, #ee8c8c);
  border: none;
  border-radius: 30px;
  position: absolute;
  top: 0;
  right: 0;
  box-shadow: 0 15px 20px rgba(206, 151, 151, 0.5);
}
.main .content__form .form-submit-btn:hover {
  background: #f8bfbf;
  cursor: pointer;
}
.main .content .form-error-msg {
  color: #f96262;
  font-size: 0.8rem;
  padding: 0 0 50px 26px;
}

@media (min-width: 900px) {
  .logo {
    position: absolute;
    left: calc((57vw - 455px) / 2);
    top: 65px;
  }
  .logo__img {
    width: 160px;
  }

  .main {
    flex-direction: row;
    justify-content: center;
    background-image: url(../images/bg-pattern-desktop.svg);
    background-repeat: no-repeat;
    background-size: contain;
  }
  .main .hero {
    width: 43%;
    min-height: 100vh;
    background-image: url(../images/hero-desktop.jpg);
  }
  .main .content {
    width: 57%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .main .content__title {
    font-size: 4rem;
    text-align: left;
    max-width: 400px;
    line-height: 1.1;
  }
  .main .content__txt {
    text-align: left;
    max-width: 400px;
    line-height: 1.8;
  }
  .main .content__form {
    max-width: 420px;
    display: block;
  }
  .main .content__form .form-input {
    padding: 22px 30px;
  }
  .main .content__form .form-error-icon {
    right: 105px;
  }
  .main .content__form .form-submit-btn {
    min-width: 95px;
  }
}
.d-none {
  display: none;
}

.attribution {
  text-align: center;
  font-size: 0.7rem;
  padding: 20px;
  color: #ce9797;
}
.attribution a {
  color: #ce9797;
}
.attribution a:hover {
  color: #413a3a;
}